/* initial stage */

.o-editor-body {
  width: 100%;
  height: auto;
  padding: 0.1px;
}

.editor-section {
  width: 100%;
  height: 100%;
}

.note_desc_editor {
  font-size: 14px;
  padding: 12px 15px 15px 15px;
  font-family: "Roboto Slab", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border: 0;
  width: 100%;
  background: transparent;
}

.note_title_editor {
  font-family: "Roboto Condensed", "segoe ui";
  font-weight: 600;
  background: transparent;
  font-size: 16px;
  padding: 10px 15px;
  width: 100%;
  border: none;
}
